import React from 'react'
import Projects from '../components/Projects'
const Portfolio = () => {
  return (
    <section id='portfolio' className='section
bg-primary min-h-[1400px]'>
<div className='container mx-auto'>
<div className='flex flex-col items-center
text-center'>
<h2 className='section-title
before:content-portfolio relative
before:absolute before: opacity-40
before:-top-[2rem] before:-left-3/4
before:hidden before:lg:block'>
My latest work
</h2>
<p className='subtitle'>
I'm a driven individual who loves taking on challenging projects.<br/>
 Recently, I've had the opportunity to work on some stunning solo projects,
  as well as collaborate with some talented groups to create exceptional work.
</p>
</div>
<Projects/>
</div>
</section>
  )
}

export default Portfolio