import React from 'react'

import { services } from '../data'

const Services = () => {
  return (
    <section id='services' className='section
     bg-tertiary'>
        <div className='container mx-auto'>
        <div className='flex flex-col items-center
text-center'>
<h2 className='section-title
before:content-services relative
before:absolute before:opacity-40
before:-top-[2rem] before:-left-28
before:hidden before:lg:block'>What I do for my clients</h2>
<p className='subtitle'>
As a web developer and designer, I'm like a master chef, carefully mixing and blending the right ingredients to create the perfect website recipe.<br />
 I can craft a website that's so delicious, 
 it'll have your customers coming back for seconds. And just like a great chef, I'll pay attention to every detail, 
 ensuring that your website is a feast for the eyes and the senses.
</p>
</div>  
<div className='grid lg:grid-cols-2 gap-8'>
{services.map((service, index) => {
const { icon, name, description} = service;
return (
<div className='bg-secondary p-6
rounded-2xl' key={index} >
{/* icon */ }
<div className='text-accent rounded-sm
w-12 h-12 flex justify-center
items-center mb-24 text-[28px]'>
{icon}
</div>
<h4 className='text-xl font-medium
mb-2'>{name}</h4>
<p>{description}</p>
</div>
);
})}
</div>
        </div>

    </section>
  )
}

export default Services